export default function initVideo(target) {
  let playVideo = $('.js-btn-video');
  let id = target;
  let player = null;

  if ( !$('body').hasClass('script-youtube') ) {
    const tag = document.createElement('script');
    const firstScriptTag = document.getElementsByTagName('script')[0];
    tag.src = 'https://www.youtube.com/iframe_api';
    firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);

    $('body').addClass('script-youtube');
  }
  

  window.onYouTubeIframeAPIReady = function() {
    player = new YT.Player(id, {
      height: '100%',
      width: '100%',
      videoId: id,
      playerVars: {
        controls: 1,
        loop: 1,
        showinfo: 0,
        rel: 0
      },
      events: {
        onReady: function() {

          let iframe = $(`#${id}`);
          let iframeParent = iframe.parents('.js-video');
          let btnPLay = iframeParent.next();
          let title = iframeParent.next().next();

          $('.js-title-video').removeClass('hide');
          $('.js-btn-video').removeClass('hide');
          $('.js-video').removeClass('is-active');

          btnPLay.addClass('hide');
          title.addClass('hide');
          iframeParent.addClass('is-active');

          player.playVideo();
          
          iframe.addClass('iframe');
          iframe.parents('.js-video').append($('<div/>', {
            class: 'player',
            id: id
          }));

        }
      }
    });

  };

}
